body {
  background-image: url(./images/home-wallpaper.jpg);
  background-size: cover;
  background-attachment: fixed;
  overflow-x: hidden;
  font-family: "Roboto", sans-serif;
}

.section::-webkit-scrollbar {
  display: none;
}

.logo {
  width: 3vw;
}

.header {
  width: 100vw;
  height: 5vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  z-index: 10;
}

.header img {
  margin-left: 3vw;
}

.header-navigation {
  width: 30vw;
  height: 5vw;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: 3vw;
}

.header-navigation li {
  list-style: none;
}

.nav-button {
  text-decoration: none;
  color: white;
  font-family: "Roboto", sans-serif;
  font-size: 0.9vw;
  transition: 0.5s ease;
}

.nav-button:hover {
  color: lightgray;
}

.nav-button-active {
  color: gray;
}

.section {
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  position: absolute;
  top: 0;
  right: 0;
}

.home-text {
  color: white;
  position: absolute;
  top: 25%;
  left: 10%;
}

.home-text h2 {
  font-size: 4.5vw;
}

.home-text p {
  font-size: 2vw;
  width: 60vw;
}

.home-button {
  width: 20vw;
  height: 5vw;
  font-size: 1.5vw;
  border-radius: 1vw;
  border: 0.15vw solid white;
  background-color: transparent;
  color: white;
  margin-top: 1.5vw;
  cursor: pointer;
  outline: none;
  transition: 0.5s ease;
}

.home-button:hover {
  color: black;
  background-color: white;
}
.fade-apper,
.fade-enter {
  opacity: 0;
  transform: translateX(100%);
  z-index: 1;
}

.fade-apper-active,
.fade-enter.fade-enter-active {
  opacity: 1;
  transform: translateX(0%);
  transition: transform 0.5s ease, opacity 0.5s ease;
}

.fade-exit {
  transform: translateX(0%);
  opacity: 1;
}

.fade-exit.fade-exit-active {
  opacity: 0;
  transform: translateX(-100%);
  transition: transform 0.5s ease, opacity 0.5s ease;
}

.introduction-container {
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 85vw;
  margin-bottom: 10vw;
}

.about {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.introduction {
  background-color: #fff;
  color: black;
  width: 85vw;
  height: 100vw;
  border-radius: 1vw;
  box-shadow: 9px 13px 28px 0px rgba(0, 0, 0, 0.85);
  overflow: hidden;
  position: relative;
}

.cards-container {
  width: 80vw;
  height: 30vw;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  position: absolute;
  top: 35vw;
  left: 2vw;
  background-color: #c34543;
  border-radius: 1vw;
}

.cards-container-title {
  position: absolute;
  top: -5.5vw;
  font-size: 1.5vw;
}

.card {
  width: 13vw;
  height: 9vw;
  margin: 1vw;
  border-radius: 1vw;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Roboto", sans-serif;
  color: black;
  box-shadow: 9px 13px 28px 0px rgba(0, 0, 0, 0.85);
  overflow: hidden;
}

.card-logo {
  margin-top: 2vw;
  width: 4vw;
}

.card-name {
  font-size: 1.2vw;
}

.about-img {
  width: 15vw;
  position: absolute;
  right: 5vw;
  top: 3vw;
}

.introduction-title {
  font-size: 2.2vw;
  position: relative;
  left: 8vw;
  top: 3.5vw;
}

.introduction-text {
  font-size: 1.2vw;
  width: 50vw;
  position: relative;
  left: 8vw;
  top: 3.5vw;
}

.portfolio-link-button {
  position: absolute;
  width: 100%;
  background-color: #c34543;
  top: 70%;
  left: 0vw;
  height: 30vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.portfolio-link-title {
  font-size: 2.2vw;
  color: white;
}

.portfolio-link-button button {
  outline: none;
  border-radius: 1vw;
  width: 20vw;
  height: 5vw;
  font-size: 1.5vw;
  box-shadow: 9px 13px 28px 0px rgba(0, 0, 0, 0.85);
  margin-top: 4vw;
  border: 0.2vw solid white;
  cursor: pointer;
  background-color: white;
  color: black;
  transition: 0.5s ease;
}

.portfolio-link-button button:hover {
  background-color: lightgray;
  border-color: lightgray;
}

.projects {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.projects-container {
  margin-top: 16vw;
  margin-bottom: 5vw;
  width: 90%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 5vw;
}

.project {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 42vw;
  height: 20vw;
  background-color: #fff;
  border-radius: 1vw;
  box-shadow: 9px 13px 28px 0px rgba(0, 0, 0, 0.85);
  overflow: hidden;
  position: relative;
}

.project-img {
  width: 100%;
  height: auto;
  opacity: 0.7;
}

.project-title {
  width: 95%;
  position: absolute;
  top: 25%;
  color: white;
  font-size: 1.5vw;
}

.project-text-container {
  width: 100%;
  height: 10vw;
  background-color: #fff;
  position: absolute;
  top: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.project-description {
  width: 95%;
  font-size: 1vw;
}

.project-buttons-container {
  width: 90%;
  display: flex;
  justify-content: space-around;
  margin-top: 0.5vw;
}

.project-button {
  width: 7vw;
  height: 2.5vw;
  border-radius: 1vw;
  outline: none;
  background-color: white;
  border-color: transparent;
  box-shadow: 9px 13px 28px 0px rgba(0, 0, 0, 0.85);
  cursor: pointer;
  font-size: 0.9vw;
  transition: 0.3s ease;
}

.project-button a {
  text-decoration: none;
  color: black;
}

.project-button:hover {
  background-color: lightgray;
}

.contact {
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-container {
  width: 90%;
  display: flex;
  justify-content: space-around;
}

.contact-container a {
  text-decoration: none;
  color: black;
}

.contact-card {
  cursor: pointer;
  width: 20vw;
  height: 20vw;
  background-color: white;
  box-shadow: 9px 13px 28px 0px rgba(0, 0, 0, 0.85);
  border-radius: 4vw;
  display: flex;
  align-items: center;
  overflow: hidden;
  position: relative;
  transition: 0.3s ease;
}

.contact-card:hover {
  width: 30vw;
}

.contact-image {
  width: 20vw;
}

.contact-text-container h2 {
  font-size: 1.5vw;
  width: 10vw;
}

@media screen and (max-width: 1024px) {
  .section {
    height: auto;
  }

  .logo {
    width: 10vmin;
  }

  .header {
    height: 20vmin;
  }

  .header img {
    margin-left: 5vmin;
  }

  .header-navigation {
    width: 70vmin;
    height: auto;
    margin-right: 5vmin;
  }

  .nav-button {
    font-size: 2.5vmin;
  }

  .home {
    height: 100vh;
  }

  .home-text {
    position: relative;
    top: 25%;
    left: 0;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .home-text h2 {
    font-size: 10vmin;
    width: 100%;
    text-align: center;
  }

  .home-text p {
    font-size: 6vmin;
    width: 95%;
    text-align: center;
  }

  .home-button {
    width: 45vmin;
    height: 15vmin;
    font-size: 3vmin;
    border-radius: 10vmin;
    border: 0.2vmin solid white;
    margin-top: 1.5vmin;
  }

  .introduction-container {
    width: 100vw;
    margin-top: 25vmin;
  }

  .introduction {
    width: 85vw;
    height: auto;
    border-radius: 2vmin;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .cards-container {
    padding-top: 10vmin;
    padding-bottom: 10vmin;
    margin-top: 25vmin;
    margin-bottom: 25vmin;
    width: 100%;
    height: auto;
    display: grid;
    grid-gap: 15vmin;
    grid-template-columns: 30vmin 30vmin;
    justify-content: center;
    align-items: center;
    position: static;
    border-radius: 0;
  }

  .cards-container-title {
    position: absolute;
    font-size: 2.8vmin;
    width: 100%;
    top: 75vmin;
    text-align: center;
    margin-bottom: 5vmin;
  }

  .card {
    width: 30vmin;
    height: 30vmin;
    margin: 0vw;
    border-radius: 1vmin;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .card-logo {
    margin-top: 2vmin;
    width: 20vmin;
  }

  .card-name {
    font-size: 2.5vmin;
  }

  .about-img {
    width: 30vmin;
    position: static;
  }

  .introduction-title {
    margin-top: 10vmin;
    font-size: 5vmin;
    position: static;
    width: 100%;
    text-align: center;
  }

  .introduction-text {
    font-size: 3.5vmin;
    text-align: center;
    width: 90%;
    position: static;
  }

  .portfolio-link-button {
    position: static;
    width: 100%;
    height: 50vmin;
  }

  .portfolio-link-title {
    font-size: 4vmin;
    width: 90%;
    text-align: center;
    color: white;
  }

  .portfolio-link-button button {
    border-radius: 3vmin;
    width: 40vmin;
    height: 10vmin;
    font-size: 3vmin;
    margin-top: 4vmin;
    border: 0.5vmin solid white;
    transition: 0.5s ease;
  }

  .projects {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .projects-container {
    margin-top: 30vmin;
    margin-bottom: 10vmin;
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .project {
    width: 90vmin;
    height: 60vmin;
    border-radius: 2vmin;
  }

  .project-title {
    font-size: 5vmin;
  }

  .project-text-container {
    width: 100%;
    height: 30vmin;
  }

  .project-description {
    width: 95%;
    font-size: 3vmin;
  }

  .project-buttons-container {
    margin-top: -1vmin;
  }

  .project-button {
    width: 30vmin;
    height: 7vmin;
    border-radius: 2vmin;
    font-size: 3vmin;
  }

  .project-button a {
    text-decoration: none;
    color: black;
  }

  .project-img {
    height: 60vmin;
  }

  .contact {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 30vmin;
  }

  .contact-container {
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .contact-card {
    width: 90vw;
    height: 50vmin;
    margin: 3vmin;
    border-radius: 10vmin;
  }

  .contact-card:hover {
    width: 90vw;
  }

  .contact-image {
    width: 50vmin;
  }

  .contact-text-container h2 {
    font-size: 6vmin;
    width: 10vw;
  }
}
